}

.terms {
  padding: 44px 0 0 0;
  background-color: #ffffff;
}

.terms__text {
  width: 273px;
  margin: 0 auto;
  padding: 0;
  font-style: normal;
  font-weight: 400;
  font-size: 14px;
  line-height: 30px;
  font-family: "Open Sans", "Verdana", sans-serif;
  text-align: center;
}

.terms__text--additional {
  display: none;
}

.form__name-fields {
  border: none;
}

.form__name-field-box {
  padding-top: 29px;
  padding-bottom: 16px;
}

.form__name-fields-label {
  display: block;
