  width: 48px;
  height: 43px;
  margin-left: 35px;
  background: url("../img/fill.svg") 0 0/48px 43px #f2f2f2;
  border: none;
  cursor: pointer;
}

.image-editor__contrast-tool-button {
  width: 32px;
  height: 32px;
  margin-left: 35px;
  background: url("../img/contrast.svg") 0 0/32px 32px #f2f2f2;
  border: none;
  cursor: pointer;
}

