  display: block;
  width: 240px;
  height: 24px;
}

.image-editor__tool-slider {
  width: 100%;
  color: transparent;
  background: transparent;
  border-color: transparent;
  appearance: none;
}
.image-editor__tool-slider::-moz-range-thumb {
  width: 10px;
  height: 10px;
  margin-top: -6px;
  background: #ffffff;
  border: 2px solid #d22856;
  border-radius: 50%;
  cursor: pointer;
}
.image-editor__tool-slider::-webkit-slider-thumb {
  width: 14px;
  height: 14px;
  margin-top: -6px;
  background: #ffffff;
  border: 2px solid #d22856;
  border-radius: 50%;
  cursor: pointer;
  appearance: none;
}
.image-editor__tool-slider::-webkit-slider-runnable-track {
  box-sizing: border-box;
  width: 240px;
  height: 2px;
  margin-bottom: 5px;
  background: #000000;
  cursor: pointer;
}
.image-editor__tool-slider::-moz-range-track {
